</template>


<script>

    import '../sass/main.scss';
    import 'reset-css';
    import {
        Facebook,
        Twitter,
        Telegram
    } from 'vue-socialmedia-share';
    // import SocialSharing from 'vue-social-sharing'



    export default {
        components: {
            Facebook,
            Twitter,
            Telegram
        },
        name: 'kvartalalley',
        data: function () {
            return {
                question: 0,
                answer: [0, 0, 0, 0, 0, 0, 0, 0, 0],
                render: false,
                showButton: true,
                buttonText: "Почати гру",
                path: window.location.href
            }
        },
        watch: {
            question: function () {
                if (this.question > 0) {
                    this.buttonText = "Далі"
                }
                if (this.question === 9) {
                    this.buttonText = "Отримати результат тесту"
                }
                if (this.question === 10) {
                    this.buttonText = "Дізнатись більше";
                    this.showButton = true;
                }
                this.leave();
            },
            render: function () {
                this.showHideButton();
            }
        },
        methods: {
            selectAnswer: function (answer) {
                if (answer === 1) {
                    this.$set(this.answer, this.question - 1, 1)
                }
                if (answer === 2) {
                    this.$set(this.answer, this.question - 1, 2)
                }

                if (this.question === 2) {
                    this.nextQuestion()
                    this.render = false;
                }
                else {
                    this.render = true;
                }
            },
            nextQuestion: function () {
                if (this.question < 10) {
                    this.question++;
                    this.showButton = false;
                    this.render = false;
                }
                else {
                    window.location = 'https://kvartal-aley.com';
                }

            },
            showHideButton: function () {
                if (this.render === true || this.question === 0 || this.question === 10) {
                    this.showButton = true;
                }
                else {
                    this.showButton = false
                }
            },
            backToPreviousStep: function () {
                if (this.render == false) {
                    this.question--;
                    this.render = false;
                    this.$set(this.answer, this.question - 1, 0)
                    this.$set(this.answer, this.question, 0)
                    console.log(this.answer[this.question - 1] + 'this.question' + this.question)
                }
                else {
                    this.render = false;
                    this.$set(this.answer, this.question - 1, 0)
                }

            },
            leave: function () {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
            },
            doAgain: function () {

                this.step = 0;

                for( let i = 0; i <this.answer.length; i++){
                    this.$set(this.answer, i, 0)
                }
                console.log(this.answer);
                this.question = 0;
                this.render = false;
                this.showButton = true;
            }
        }
    }
</script>

